angular.module("am.date-picker").run(["$templateCache", function($templateCache) {$templateCache.put("am-date-picker.html","<div style=\"outline-style: none;\">\r\n    <!-- Date picker input -->\r\n    <div>\r\n        <md-input-container class=\"md-icon-float md-icon-right\">\r\n            <label>{{ amDatePicker.inputLabel }}</label>\r\n            <md-icon ng-show=\"amDatePicker.showInputIcon\"\r\n                     md-svg-icon=\"{{ amDatePicker.calendarIcon }}\"\r\n                     class=\"am-date-picker__calendar-icon\"></md-icon>\r\n            <input ng-model=\"amDatePicker.ngModelMomentFormatted\"\r\n                   ng-click=\"amDatePicker.openPicker($event)\"\r\n                   class=\"am-date-picker__input\"\r\n                   type=\"text\"\r\n                   readonly=\"readonly\">\r\n            <md-icon ng-show=\"amDatePicker.allowClear\"\r\n                     ng-click=\"amDatePicker.clearDate()\"\r\n                     md-svg-icon=\"{{ amDatePicker.clearIcon }}\"\r\n                     class=\"am-date-picker__clear-icon\"></md-icon>\r\n        </md-input-container>\r\n    </div>\r\n\r\n\r\n<script type=\"text/ng-template\" id=\"am-date-picker_content.tmpl.html\">\r\n    <md-dialog class=\"am-date-picker__container\" aria-label=\"Datepicker Content\" ng-cloak>\r\n    <div layout=\"column\">\r\n\r\n        <!-- Current date -->\r\n        <div layout-align=\"center\" class=\"am-date-picker__header-current-date\">\r\n            <p>{{ amDatePicker.moment(amDatePicker.ngModel).format(\'YYYY\') }}</p>\r\n            <h2>{{ amDatePicker.moment(amDatePicker.ngModel).format(amDatePicker.popupDateFormat) }}</h2>\r\n        </div>\r\n\r\n    </div>\r\n\r\n    <!-- Date picker: content -->\r\n    <div ng-hide=\"amDatePicker.yearSelection\" class=\"am-date-picker__content am-date-picker__animate-year-selection\">\r\n        <div layout=\"row\" layout-align=\"space-between center\" class=\"am-date-picker__month-year\">\r\n            <md-button ng-click=\"amDatePicker.previousMonth()\"\r\n                       class=\"md-icon-button\">\r\n                <md-icon aria-label=\"Prev\"\r\n                         class=\"am-date-picker__chevron\"\r\n                         md-svg-icon=\"{{ amDatePicker.prevIcon }}\"></md-icon>\r\n\r\n            </md-button>\r\n\r\n            <md-button ng-click=\"amDatePicker.displayYearSelection()\"\r\n                       class=\"am-date-picker__month-year\">\r\n                {{ amDatePicker.ngModelMoment.format(\'MMMM YYYY\') }}\r\n            </md-button>\r\n\r\n             <md-button ng-click=\"amDatePicker.nextMonth()\"\r\n                        class=\"md-icon-button\">\r\n                <md-icon aria-label=\"Next\"\r\n                         class=\"am-date-picker__chevron\"\r\n                         md-svg-icon=\"{{ amDatePicker.nextIcon }}\"></md-icon>\r\n            </md-button>\r\n        </div>\r\n\r\n        <!-- Calendar -->\r\n        <div class=\"am-date-picker__calendar am-date-picker__animate-month-change\"\r\n             ng-class=\"{\'next\': amDatePicker.next,\r\n                        \'prev\': !amDatePicker.next}\"\r\n             ng-if=\"amDatePicker.change\">\r\n\r\n            <!-- Weekdays -->\r\n            <div style=\"padding-left: 12px; padding-right: 20px;\" layout layout-align=\"space-between\">\r\n                <span ng-repeat=\"day in amDatePicker.daysOfWeek\"\r\n                      class=\"am-date-picker__weekdays\">\r\n                    {{ day }}\r\n                </span>\r\n            </div>\r\n\r\n            <!-- Days -->\r\n            <span ng-repeat=\"x in amDatePicker.emptyFirstDays\"\r\n                  class=\"am-date-picker__days\">&nbsp;</span>\r\n\r\n            <div class=\"am-date-picker__days am-date-picker__day\"\r\n                 ng-class=\"{ \'am-date-picker__day--is-selected\': day.selected,\r\n                             \'am-date-picker__day--is-today\': day.today && !day.selected,\r\n                             \'am-date-picker__day--is-disabled\': day.disabled }\"\r\n                 ng-repeat=\"day in amDatePicker.days\">\r\n                <a ng-click=\"amDatePicker.select(day)\">{{ day ? day.format(\'D\') : \'\' }}</a>\r\n            </div>\r\n\r\n        </div>\r\n    </div>\r\n\r\n    <!-- Year selection -->\r\n    <div class=\"am-date-picker__year-selector am-date-picker__animate-year-selection\"\r\n         ng-show=\"amDatePicker.yearSelection\">\r\n        <a class=\"am-date-picker__year\"\r\n           ng-repeat=\"year in amDatePicker.years\"\r\n           ng-class=\"{ \'am-date-picker__year--is-active\':\r\n                        year == amDatePicker.moment(amDatePicker.ngModel).format(\'YYYY\') }\"\r\n           ng-click=\"amDatePicker.selectYear(year)\">\r\n            <p>{{ year }}</p>\r\n        </a>\r\n    </div>\r\n\r\n    <!--Actions-->\r\n    <md-dialog-actions layout=\"row\" class=\"md-actions am-date-picker__actions\">\r\n        <md-button ng-click=\"amDatePicker.today()\"\r\n                   ng-disabled=\"amDatePicker.isTodayDisabled\"\r\n                   ng-if=\"amDatePicker.todayButton\">\r\n            {{ amDatePicker.todayButton }}\r\n        </md-button>\r\n        <span flex></span>\r\n        <div ng-if=\"!amDatePicker.yearSelection\">\r\n            <md-button ng-click=\"dialog.cancel()\"\r\n                       ng-if=\"amDatePicker.cancelButton\">\r\n                {{ amDatePicker.cancelButton }}\r\n            </md-button>\r\n            <md-button ng-click=\"dialog.hide()\">OK</md-button>\r\n        </div>\r\n        <md-button ng-click=\"amDatePicker.hideYearSelection()\"\r\n                   ng-if=\"amDatePicker.yearSelection\">{{ amDatePicker.backButtonText }}</md-button>\r\n    </md-dialog-actions>\r\n    </md-dialog>\r\n</script>\r\n\r\n</div>\r\n");}]);